type Post = {
  id: number;
  title: string;
  content: string;
  author: string;
  created_at: string;
  image?: string | null;
};

const API_BASE = process.env.NEXT_PUBLIC_API_BASE || "http://localhost:8000";

async function fetchPosts(): Promise<Post[]> {
  const res = await fetch(`${API_BASE}/api/posts/`, { next: { revalidate: 30 } });
  if (!res.ok) return [];
  return res.json();
}

export const metadata = {
  title: "Latest News | DCU Athletics",
};

export default async function NewsPage() {
  const posts = await fetchPosts();
  return (
    <div>
      <h1 className="text-3xl font-bold mb-6 text-blue-700">Latest News</h1>
      <div className="space-y-6">
        {posts.map((post) => {
          const imageUrl = post.image
            ? post.image.startsWith('http')
              ? post.image
              : `${API_BASE}${post.image}`
            : null;
          return (
            <article key={post.id} className="bg-white rounded-xl shadow p-5">
              <h2 className="text-2xl font-semibold">{post.title}</h2>
              <p className="text-gray-600 text-sm mb-3">By {post.author} Â· {new Date(post.created_at).toLocaleDateString()}</p>
              {imageUrl && (
                // eslint-disable-next-line @next/next/no-img-element
                <img src={imageUrl} alt={post.title} className="rounded mb-4 w-full object-cover" />
              )}
              <p className="whitespace-pre-wrap">{post.content}</p>
            </article>
          );
        })}
        {posts.length === 0 && <p className="text-gray-600">No news yet.</p>}
      </div>
    </div>
  );
}


